@import '~supergrids.css/src/scss/mixins';

$yellow-000: hsl(55,100%,65%);
$yellow-100: hsl(45,100%,50%);

$yellow-025: mix($yellow-000,$yellow-100,25%);
$yellow-050: mix($yellow-000,$yellow-100,50%);
$yellow-075: mix($yellow-000,$yellow-100,75%);

$gray-hue: 230;
$gray-sat: 10%;

$gray-050: hsl($gray-hue,$gray-sat,05%);
$gray-100: hsl($gray-hue,$gray-sat,10%);
$gray-150: hsl($gray-hue,$gray-sat,15%);
$gray-200: hsl($gray-hue,$gray-sat,20%);
$gray-250: hsl($gray-hue,$gray-sat,25%);
$gray-300: hsl($gray-hue,$gray-sat,30%);
$gray-350: hsl($gray-hue,$gray-sat,35%);
$gray-400: hsl($gray-hue,$gray-sat,40%);
$gray-450: hsl($gray-hue,$gray-sat,45%);
$gray-500: hsl($gray-hue,$gray-sat,50%);
$gray-550: hsl($gray-hue,$gray-sat,55%);
$gray-600: hsl($gray-hue,$gray-sat,60%);
$gray-650: hsl($gray-hue,$gray-sat,65%);
$gray-700: hsl($gray-hue,$gray-sat,70%);
$gray-750: hsl($gray-hue,$gray-sat,75%);
$gray-800: hsl($gray-hue,$gray-sat,80%);
$gray-850: hsl($gray-hue,$gray-sat,85%);
$gray-900: hsl($gray-hue,$gray-sat,90%);
$gray-950: hsl($gray-hue,$gray-sat,95%);

$rightSidebarWidth: 256px;
