/* ubuntu-300 - latin */
@font-face {
	font-family: 'Ubuntu';
	font-style: normal;
	font-weight: 300;
	src:
		local('Ubuntu Light'),
		local('Ubuntu-Light'),
		url('ubuntu-v14-latin-300.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
		url('ubuntu-v14-latin-300.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* ubuntu-300italic - latin */
@font-face {
	font-family: 'Ubuntu';
	font-style: italic;
	font-weight: 300;
	src:
		local('Ubuntu Light Italic'),
		local('Ubuntu-LightItalic'),
		url('ubuntu-v14-latin-300italic.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
		url('ubuntu-v14-latin-300italic.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
