@import '../../_variables';

#Login{
	@include super-center;
	@include stretch-to-fit;
	position: absolute;
	background-color: rgba(black,.5);
	transition:
		opacity .5s,
		visibility .5s;
	>section{
		border-radius: 6px;
		box-shadow: 0 0 0 1px rgba(var(--textRGB),.1);
		overflow: hidden;
		backdrop-filter: blur(32px);
		transition: transform .5s;
		@supports not (backdrop-filter: blur(32px)){
			background-color: rgba($gray-050,.875);
		}
	}
	&:not(.active){
		opacity: 0;
		visibility: hidden;
		>section{
			transform: scale(.875);
		}
	}
	.cam-feed-container{
		width: 640px;
		height: 480px;
		border-radius: 6px;
		box-shadow: 0 1px rgba(var(--textRGB),.1);
		>img{
			width: 640px;
			height: 480px;
		}
	}
	input{
		border-radius: 0px 4px 4px 0px;
		// border-style: solid;
		// border-color: rgba(var(--textRGB),.125);
		// border-width: 1px 1px 1px 0;
		// box-shadow: none;
		// &:focus{
		// 	border-color: rgba(var(--accentRGB),1);
		// }
	}
}
